<template>
  <div class="enroll">
    <div class="header"></div>
    <div class="ra">
      <div class="img">
        <van-uploader class="imgu" :max-count="1" />
      </div>
    </div>
    <van-cell-group>
      <van-field
        v-model="user.value"
        type="text"
        label="用户名:"
        placeholder="执笔于此"
        :error-message="user.errorMessage"
        @input="validUP(user.value,'validUsername',{key:'user',errorMessage:'用户名支持4-8个(字母数字_)组合，必须字母开头'})"
      />
      <van-field
        v-model="pwd1.value"
        :type="pwd1.type"
        label="密码:"
        placeholder="执笔于此"
        :right-icon="pwd1.icon"
        :error-message="pwd1.errorMessage"
        @click-right-icon="checkPassword('pwd1')"
        @input="validUP(pwd1.value, 'validPwd', {key: 'pwd1', errorMessage: '密码支持6-16个(字母数字_)组合，必须含有大小写字母'})"
      />

      </van-cell-group>
      <div class="register-form">
        <van-button :loading="isLoading" type="info" round block loading-text="登录中..." 
        text="登录" @click="enroll()" :disabled="!isValid"/>

      </div>
      <!-- 跳回注册页面 -->
      <div class="book">
          <router-link class="book-text" to="/register">没有账号？注册</router-link></div>
    </div>
</template>

<script>

  import {validForm} from '../tool/validForm'

  import {createNamespacedHelpers} from 'vuex'

  //基于enrollModule的命名空间导入mapState、mapMutations 辅助函数

  const {mapState, mapMutations} = createNamespacedHelpers('enrollModule');

  export default {
    name: 'enroll',

    computed: {
      ...mapState(['isLoading', 'user', 'pwd1', 'isValid','Toast'])
    },

    methods: {

      ...mapMutations(['checkPassword']),

      //登录
      enroll() {
        // console.log('a');
        this.$store.commit('enrollModule/enroll', true);

        let randomTime = Math.random() * 5 * 1000;

        setTimeout(() => {
          //获取所有用户
          let users = localStorage.getItem('users');

          users = users ? JSON.parse(users) : [];

          if (users.length === 0) {
            this.$toast({
              duration: 2000,
              message: '该用户不存在'
            })
            this.$store.commit('enrollModule/enroll', false);
            return;
          }

          //查找用户
          for (let i = 0; i < users.length; i++) {
            if (users[i].username == this.user.value) {
              //如果该用户存在
              //匹配密码
                
              if (users[i].pwd == this.pwd1.value) {
                this.$toast({
                  duration: 2000,
                  message: '登录成功'
                })
                this.$router.push({name:'bookshelf'})
                this.$store.commit('enrollModule/enroll', false);
                return;
              } else {
                this.$toast({
                  duration: 2000,
                  message: '用户名或者密码不正确'
                })
                this.$store.commit('enrollModule/enroll', false);
                return;
              }
            }
          }

          //用户不存在
          this.$toast({
            duration: 2000,
            message: '该用户不存在'
          })
          this.$store.commit('enrollModule/enroll', false);

        }, randomTime)
        
      },

      //验证用户名或者密码
      validUP(value, fnName, o) {
        let isValid = validForm[fnName](value);

        // console.log('isValid ==> ', isValid);

        o.isValid = isValid;

        this.$store.commit('enrollModule/validUP', o);

        this.$store.commit('enrollModule/passForm');
      }

    }
  }
</script>
<style lang="less" scoped>

.enroll {
  height: 100%;
  background: url("../assets/bg.jpg") no-repeat center center;
  background-size: cover;
  .header {
    height: 24px;
  }
  .ra {
    padding: 10px;
    height: 200px;
    position: relative;
    .img {
      width: 100px;
      height: 100px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: rgb(218, 218, 218);
      border-radius: 50%;
      box-shadow: 0 0 0 5px rgb(218, 218, 218);
      text-align: center;
      .imgu {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: rgb(218, 218, 218);
        margin: 0;
      }
    }
  }
  .book {
    width: 100%;
    text-align: center;
    margin-top: 10px;
    .book-text {
      text-decoration: underline;
      color: #8d8585;
      &:active {
        color: #000000;
      }
    }
  }
}
</style>
<style lang="less" >
.imgu .van-uploader__upload {
  margin: 0;
  border-radius: 50%;
  width: 90px;
  height: 90px;
  .van-uploader__input {
    border-radius: 50px;
  }
}
.van-cell-group {
  background: none;
  .van-cell {
    margin: 0 20px;
    width: calc(100% - 40px);
    border-radius: 50px;
    margin-bottom: 20px;
    background: rgba(255, 255, 255, 0.75);
    .van-field__label {
      width: 2rem;
      text-align: center;
    }
  }
}
.van-button--block {
  margin: 40px 20px 0;
  width: calc(100% - 40px);
}
.van-button--info {
  background-color: #4d5555;
  border: 0.02667rem solid #4d5555;
}
.van-uploader__preview{
  margin: 0;
}
.van-uploader__preview-image{
  border-radius: 50% !important;
}
</style>